:root {
  --primaryLight-color: #94618e;
  --primaryDark-color: #49274a;
  --secondaryDark-color: #f4decb;
  --secondaryLight-color: #f8eee7;
}

.App {
  text-align: left;
}
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey;
  border-radius: 5px;
  background-color: white;
}
::-webkit-scrollbar-track-piece:start {
  border-radius: 5px;
  box-shadow: inset 0 0 2px grey;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: -webkit-linear-gradient(45deg, var(--secondaryDark-color), white, var(--secondaryDark-color));
  box-shadow: inset 0 0 2px grey;
  border-radius: 5px;
}
* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
a {
  word-break: break-all;
}

a:link,
a:visited,
a:active {
  color: var(--primaryLight-color);
  text-decoration: none;
  transition: 0.5s;
}
a:hover {
  background: -webkit-linear-gradient(
    33deg,
    var(--primaryDark-color),
    var(--primaryLight-color),
    var(--primaryDark-color)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.page-header {
  display: none;
}
.page-container {
  display: flex;
  width: 100vw;
  height: 100vh;
  color: var(--primaryDark-color);
}
.left-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.right-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
}
.right-container-portion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 95%;
  margin: 10px 0;
  border-radius: 5px;
  border: 3px var(--secondaryDark-color) double;
}
.card-head {
  display: flex;
  align-items: center;
  height: 40px;
  width: 100%;
  padding: 10px;
  background-color: var(--secondaryLight-color);
  border-bottom: 1px var(--secondaryDark-color) dashed;
  font-size: 25px;
}
.card-body {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 40px;
  width: 100%;
  padding: 10px;
  font-size: 15px;
}
.card:hover .card-head {
  background: -webkit-linear-gradient(
    75deg,
    var(--secondaryLight-color),
    var(--secondaryDark-color),
    var(--primaryLight-color)
  );
}
.line {
  position: absolute;
  left: 30px;
  height: 90%;
  width: 5px;
  border-radius: 3px;
  background-color: var(--secondaryLight-color);
  z-index: -1;
}
.card-body:hover .line {
  background: -webkit-linear-gradient(
    45deg,
    var(--secondaryDark-color),
    var(--secondaryLight-color),
    var(--secondaryDark-color)
  );
}
.timeline-object {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  position: relative;
  width: 100%;
  padding: 0 0 0 7px;
  margin-top: 5px;
  /* background-color: red; */
}
.timeline-point {
  position: absolute;
  top: 25px;
  left: 15px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: var(--primaryLight-color);
}
.timeline-object:hover .timeline-point {
  background: -webkit-linear-gradient(var(--primaryLight-color), var(--primaryDark-color));
}
.timeline-date {
  position: absolute;
  left: 10px;
  font-size: 10px;
  padding: 2px;
  font-color: var(--primaryLight-color);
  background-color: white;
  border-radius: 3px;
  border: 1px dashed var(--primaryLight-color);
}

.timeline-object:hover .timeline-date {
  color: white;
  background: -webkit-linear-gradient(var(--primaryLight-color), var(--primaryDark-color));
}
.timeline-list {
  margin-left: 30px;
  padding: 5px 10px;
  width: 600px;
  max-width: 80%;
  min-height: 80px;
  background: var(--secondaryLight-color);
  position: relative;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  font-size: 12px;
}
.timeline-object:hover .timeline-list {
  background: -webkit-linear-gradient(
    -75deg,
    var(--secondaryLight-color),
    var(--secondaryDark-color),
    var(--primaryLight-color)
  );
}
.timeline-list:before {
  content: '';
  position: absolute;
  right: 99%;
  top: 20px;
  width: 0;
  height: 0;
  border-top: 13px solid transparent;
  border-right: 26px solid var(--secondaryLight-color);
  border-bottom: 13px solid transparent;
  z-index: -1;
}

.list-head--wrap {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-end;
}
.list-head {
  font-size: 16px;
  font-weight: 800;
  color: var(--primaryLight-color);
}
.list-subhead {
  font-size: 14px;
  font-weight: 600;
}

.icon {
  display: flex;
  align-items: center;
  font-size: 20px;
  width: 5%;
  min-width: 30px;
  height: 30px;
  color: var(--primaryLight-color);
  border-right: var(--primaryLight-color) 2px dashed;
  margin: 2px;
}

@media only screen and (min-width: 766px) {
  .page-header {
    background: -webkit-linear-gradient(45deg, var(--secondaryDark-color), white, var(--secondaryDark-color));
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 20px;
    height: 10vh;
    width: 100vw;
  }

  .page-header h2 {
    text-align: center;
    background: -webkit-linear-gradient(
      33deg,
      var(--primaryDark-color),
      var(--primaryLight-color),
      var(--primaryDark-color)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .page-container {
    height: 90vh;
    flex-direction: row;
  }
  .left-container {
    border-right-width: 0px;
    width: 30%;
    height: 100%;
    overflow: auto;
    border-right: 2px var(--secondaryDark-color) solid;
  }
  .right-container {
    width: 70%;
    height: 100%;
    overflow: auto;
  }
}
@media only screen and (max-width: 766px) {
  .page-container {
    flex-direction: column;
    overflow: auto;
  }
  .left-container {
    width: 100%;
  }
  .right-container {
    width: 100%;
  }
}
.page-break {
  display: none;
}
@media print {
  .page-header {
    background: -webkit-linear-gradient(45deg, var(--secondaryDark-color), white, var(--secondaryDark-color));
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 20px;
    height: 40px;
    width: 100vw;
  }

  .page-header * {
    text-align: center;
    background: -webkit-linear-gradient(
      33deg,
      var(--primaryDark-color),
      var(--primaryLight-color),
      var(--primaryDark-color)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 16px;
  }
  .page-container {
    width: 100vw;
    height: 100%;
    flex-direction: column;
    align-items: center;
  }
  .left-container {
    border-right-width: 0px;
    flex-direction: row;
    justify-content: space-between;
    width: 95%;
    height: 20vh;
    border-right-width: 0px;
  }
  .left-container > * {
    width: 48%;
  }
  .right-container {
    width: 100%;
    height: 100%;
  }
  .right-container-portion {
    height: 78vh;
  }
}
